@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
.Title{background-color:#3e2b70;color:#ffffff;padding:50px 0;margin-bottom:50px;display:flex;justify-content:center}.Title-Content{display:flex;align-items:center;justify-content:flex-start;width:70%}.Title-Content h2,.Title-Content p{margin:0;padding:0 10px}.Title-Content h2{font-size:36px;padding-left:0}.Title-Content p{font-size:14px}

.ClubTag{display:inline-block;background-color:#3e2b70;color:white;padding:2px 8px;border-radius:10px;font-size:12px;font-weight:bold}

.PersonCard{width:350px;height:320px;padding:20px;overflow:hidden;border-radius:3px;background-color:#fbfbfb}.PersonCard-ProfileWrapper{display:flex;align-items:center;justify-content:space-between}.PersonCard-ProfileWrapper-Profile{display:flex;align-items:center;justify-content:flex-start}.PersonCard-ProfileWrapper-Profile-Image{width:100px;height:100px;object-fit:cover;border-radius:50%}.PersonCard-ProfileWrapper-Profile-Name{font-size:2rem;font-weight:bold;margin-left:20px;color:#281e4d}.PersonCard-ProfileWrapper-Generation{font-size:1.25rem;color:#444}.PersonCard-Detail{display:flex;align-items:center;justify-content:space-between;margin-top:20px}.PersonCard-Detail-Club{display:flex;align-items:center;justify-content:flex-start}.PersonCard-Detail-Club>*{margin-right:5px}.PersonCard-Detail-URL{display:flex;align-items:center;justify-content:flex-start}.PersonCard-Detail-URL>*{margin-left:5px}.PersonCard-Intro{margin-top:30px;color:#281e4d}

.PersonList{width:90%;margin:0 auto;display:grid;grid-template-columns:repeat(5, 1fr);grid-row-gap:20px;row-gap:20px;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;justify-items:center}

.InfoBox{width:90%;display:flex;align-items:flex-end;justify-content:flex-end;text-align:end}.InfoBox .AiFillInfoCircle{cursor:pointer}

*{-webkit-user-select:none;-ms-user-select:none;user-select:none;margin:0;padding:0;box-sizing:border-box;font-family:'Noto Sans KR', sans-serif}

